.header-content {
  width: 100%;
  height: 100%;
  padding: 0 50px 50px 50px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.header-menu {
  margin-bottom: 50px;
}

.header-menu-list {
  display: flex;
}

.header-menu-list > li > a {
  margin: 0px 20px 20px 20px;
  font-size: 16px;
  color: white;
  text-decoration: none;
  border-bottom: 2px solid #fff4;
}

.header-menu-list > li > a:hover {
  color: red;
  border-bottom: 2px solid #f006;
}

.header-logo {
  margin: 0 auto;
  width: 353px;
  height: 70px;
  margin-bottom: 5px;
}

.header-logo > img {
  width: 100%;
}

.header-center-text {
  text-align: center;
  font-size: 20px;
  line-height: 27px;
  color: white;
  width: 470px;
}

.header-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.header-bottom-left,
.header-bottom-right {
  width: 410px;
}

.header-bottom-left > p,
.header-bottom-right > p {
  margin: 0;
  color: #fff6;
  font-size: 16px;
}

.header-bottom-right > p {
  text-align: right;
}

.header-bottom-left > p > a,
.header-bottom-right > p > a {
  color: #fff6;
  text-decoration: none;
  border-bottom: 2px solid #fff4;
}

.header-bottom-left > p > a:hover,
.header-bottom-right > p > a:hover {
  border-bottom: 2px solid #f006;
  color: red;
  opacity: 1;
}

.header-bottom-left > h3,
.header-bottom-right > h3 {
  color: white;
  margin: 0;
  font-size: 35px;
}

.header-bottom > button {
  font-weight: bold;
  font-size: 16px;
  color: white;
  width: 144px;
  height: 47px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%), linear-gradient(94.81deg, #b2f000 0%, #58c207 47.92%, #00d287 100%);
  background-blend-mode: overlay, normal;
  border: none;
  border-radius: 7px;
  transition: all 250ms ease-in-out;
}

.header-bottom > button:hover {
  transform: translateY(-5px) scale(1.05);
  box-shadow: 0 13px 15px -10px #50b70077;
}

.about {
  width: 100%;
  background-color: #fff;
  font-weight: 700;
  padding-top: 55px;
  padding-bottom: 85px;
}

.about__title {
  font-size: 80px;
  line-height: 1.2;
  margin-bottom: 15px;
}

.about__text-wrapper {
  margin-bottom: 65px;
}

.about__text {
  color: rgba(0, 0, 0, 0.5);
  font-size: 24px;
  line-height: 1.25;
}

.about__slogan {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 768px) {
  .about__slogan {
    flex-direction: column;
    align-items: center;
  }
}

.about__slogan-start, .about__img-wrapper, .about__slogan-end {
  display: inline-block;
}

@media only screen and (max-width: 768px) {
  .about__slogan-start,
  .about__slogan-end {
    width: 70%;
    margin: 0 auto 20px;
  }
}

@media only screen and (max-width: 490px) {
  .about__slogan-start,
  .about__slogan-end {
    width: 100%;
  }
}

.about__img-wrapper {
  margin: 0 20px;
}

@media only screen and (max-width: 768px) {
  .about__img-wrapper {
    margin-bottom: 20px;
    width: 50%;
    margin: 0 auto 20px;
  }
}

@media only screen and (max-width: 530px) {
  .about__img-wrapper {
    width: 70%;
  }
}

.about__img {
  display: block;
  width: 55px;
  height: auto;
}

@media only screen and (max-width: 768px) {
  .about__img {
    width: 70%;
    margin: 0 auto;
  }
}

@media only screen and (max-width: 530px) {
  .about__img {
    width: 100%;
  }
}

.about__slogan-img {
  display: block;
  width: 100%;
  margin: 0 auto;
}

@media only screen and (max-width: 768px) {
  .about__slogan-img {
    width: 70%;
  }
}

@media only screen and (max-width: 490px) {
  .about__slogan-img {
    width: 100%;
  }
}

.speakers {
  background-color: pink;
}
